.wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: center;
  margin-top: 25px;
}
.block {
  border: 1px solid;
  border-radius: 10px;
  padding: 5px;
  width: 300px;
}
.blockTitle {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 15px;
}
